import React from "react"
import { PropTypes } from "prop-types"
import { graphql } from "gatsby"

import Layout from "components/layout"
import SEO from "components/atoms/seo"
import Hero from "components/molecules/hero"
import Projects from "components/molecules/projects"
import Footer from "components/molecules/footer"
import Work from "components/molecules/work"

const IndexPage = (query) => {
  const metadata = query.data.site.siteMetadata
  const projects = query.data.allMarkdownRemark.edges

  return (
    <Layout>
      <SEO title="Portfolio" />
      <Hero {...metadata} />
      <Projects projects={projects} />
      <Work {...metadata.work} />
      <Footer {...metadata} />
    </Layout>
  )
}

export const pageQuery = graphql`
  query HomeQuery {
    site {
      siteMetadata {
        about
        author
        categories
        intro
        location {
          city
          lat
          lng
        }
        social {
          # codepen
          dribbble
          github
          linkedin
          medium
          npm
          spotify
          # twitter
        }
        title
        work {
          company {
            name
            description
            website
          }
          interests
          since
        }
      }
    }
    allMarkdownRemark(
      limit: 10
      sort: {
        order: [DESC, DESC, ASC, ASC]
        fields: [
          frontmatter___year
          frontmatter___active
          frontmatter___order
          frontmatter___title
        ]
      }
      filter: { frontmatter: { show: { eq: true } } }
    ) {
      edges {
        node {
          frontmatter {
            active
            case
            description
            iframe
            image
            logo
            path
            show
            tags
            title
            type
            website
            year
          }
        }
      }
    }
  }
`

IndexPage.propTypes = {
  query: PropTypes.object,
}

export default IndexPage
