import React from "react"
import { PropTypes } from "prop-types"
import { Box, Heading, Paragraph, WorldMap } from "grommet"
import TextLoop from "react-text-loop"
import styled from "styled-components"
import useDarkMode from "use-dark-mode"

import Divider from "components/atoms/divider"
import Responsive from "components/atoms/responsive"
import Social from "components/atoms/social"
import Wrapper from "components/atoms/wrapper"

const StyledTextLoop = styled(TextLoop)`
  ${(props) => `color: ${props.theme.global.colors.brand}`};
`

const StyledMap = styled(Box)`
  position: absolute;
  max-width: 1200px;
  top: 0;
  right: 0;
  left: 50%;
  bottom: 50%;
`

const StyledWorldMap = styled(WorldMap)`
  right: -50%;
`

const areas = {
  small: [
    { name: "one", start: [0, 0], end: [0, 0] },
    { name: "two", start: [0, 1], end: [0, 1] },
  ],
  medium: [
    { name: "one", start: [0, 0], end: [0, 0] },
    { name: "two", start: [2, 0], end: [2, 0] },
  ],
  large: [
    { name: "one", start: [0, 0], end: [0, 0] },
    { name: "two", start: [2, 0], end: [2, 0] },
  ],
}

const Hero = ({ about, author, categories, intro, location, social }) => {
  const isDarkMode = useDarkMode(true).value

  return (
    <Wrapper
      childrenProps={{
        justify: "center",
        height: { min: "100vh" },
        pad: { vertical: "large", horizontal: "large" },
      }}
    >
      <Heading
        a11yTitle="Author"
        color={isDarkMode ? "accent-1" : "status-ok"}
        level={1}
        size="small"
        margin={{ bottom: "none" }}
      >
        {author}
      </Heading>

      <Heading
        a11yTitle="Introduction"
        level={1}
        margin={{ bottom: "none" }}
        size="large"
      >
        I build
        <br />
        <StyledTextLoop mask={true}>
          {categories.map((category, index) => (
            <span key={index}>{category}</span>
          ))}
        </StyledTextLoop>
        <br />
        for the web.
      </Heading>

      <Divider />

      <Responsive areas={areas} gap="medium">
        <Box gridArea="one">
          <Heading a11yTitle="About" level={2} margin="0">
            {about}
          </Heading>
        </Box>

        <Box gridArea="two">
          <Paragraph
            a11yTitle="More"
            color="dark-3"
            margin={{ top: "0", bottom: "medium" }}
            size="medium"
          >
            {intro}
          </Paragraph>

          {social && <Social {...social} />}
        </Box>
      </Responsive>

      {location && (
        <StyledMap overflow="hidden">
          <StyledWorldMap
            a11yTitle="Location"
            color="dark-3"
            fill={true}
            places={[
              {
                name: location.city,
                location: [location.lat, location.lng],
                color: "accent-1",
              },
            ]}
          />
        </StyledMap>
      )}
    </Wrapper>
  )
}

Hero.propTypes = {
  about: PropTypes.string.isRequired,
  author: PropTypes.string.isRequired,
  categories: PropTypes.array.isRequired,
  intro: PropTypes.string.isRequired,
  location: PropTypes.object,
  social: PropTypes.object,
}

export default Hero
