import React from "react"
import { PropTypes } from "prop-types"
import { Anchor, Box, Heading, Paragraph } from "grommet"
import styled from "styled-components"
import useDarkMode from "use-dark-mode"

import Responsive from "components/atoms/responsive"
import Wrapper from "components/atoms/wrapper"
import background from "images/waves.png"

const areas = {
  small: [
    { name: "one", start: [0, 0], end: [0, 0] },
    { name: "two", start: [0, 1], end: [0, 1] },
  ],
  medium: [
    { name: "one", start: [0, 0], end: [0, 0] },
    { name: "two", start: [2, 0], end: [2, 0] },
  ],
  large: [
    { name: "one", start: [0, 0], end: [0, 0] },
    { name: "two", start: [2, 0], end: [2, 0] },
  ],
}

const Container = styled(Box)`
  transition: background 0.75s !important;
  transition-timing-function: ease-in;

  @media screen and (min-width: 768px) {
    background-image: url(${background});
    background-repeat: no-repeat;
    background-position: top left;
  }
`

const Work = ({ since, company, interests }) => {
  const isDarkMode = useDarkMode(true).value
  const customBackground = isDarkMode ? "#181818" : "#f5f6fa"

  return (
    <Wrapper>
      <Container
        align="center"
        background={customBackground}
        margin={{
          top: "xlarge",
          bottom: "medium",
          left: "medium",
          right: "medium",
        }}
        pad="large"
        responsive={true}
        round={true}
      >
        <Responsive gap="medium" areas={areas}>
          <Box gridArea="one">
            <Heading
              a11yTitle="Work"
              color="brand"
              margin={{ top: "0", bottom: "medium" }}
              size="small"
            >
              Work
            </Heading>
            <Paragraph>
              I have {new Date().getFullYear() - since} years of experience
              building websites and applications. I&apos;m currently working at{" "}
              <Anchor
                a11yTitle={`Visit ${company.name}`}
                color="brand"
                href={company.website}
                label={company.name}
                rel="noopener"
                target="_blank"
              />
              , {company.description}
            </Paragraph>
            <Paragraph>
              Drop me a message on LinkedIn to say hello or talk about work
              opportunities.
            </Paragraph>
          </Box>

          <Box gridArea="two">
            <Heading
              a11yTitle="Interests"
              color="brand"
              margin={{ top: "0", bottom: "medium" }}
              size="small"
            >
              Interests
            </Heading>
            <Paragraph>{interests}</Paragraph>
          </Box>
        </Responsive>
      </Container>
    </Wrapper>
  )
}

Work.propTypes = {
  since: PropTypes.number.isRequired,
  company: PropTypes.shape({
    name: PropTypes.string.isRequired,
    website: PropTypes.string.isRequired,
    description: PropTypes.string.isRequired,
  }),
  interests: PropTypes.string.isRequired,
}

export default Work
